<script>
export default {
  data() {
    return {
      formData: {
        name: "",
        email: "",
        message: "",
      },
      loading: false,
      success: false,
      error: false,
      formspreeUrl: "https://formspree.io/f/mnnjoeaz", 
      primarySkills: [
        'Forretningsforstelse',
        'Dataanalyse og visualisering',
        'Python',
        'Power BI',
        'DAX',
        'SQL',
        'Power Query'
      ]
    };
  },
  methods: {
    scrollToSection(sectionId) {
      const element = document.getElementById(sectionId);
      if (element) {
        element.scrollIntoView({ 
          behavior: 'smooth',
          block: 'start'
        });
      }
    },
    async submitForm() {
      this.loading = true;
      this.success = false;
      this.error = false;

      try {
        const response = await fetch(this.formspreeUrl, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(this.formData),
        });

        if (response.ok) {
          this.success = true;
          this.formData = { name: "", email: "", message: "" };
        } else {
          this.error = true;
        }
      } catch (error) {
        this.error = true;
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>

